@import "https://fonts.googleapis.com/css2?family=Montagu+Slab:opsz,wght@16..144,100..700&display=swap";
@import "https://fonts.googleapis.com/css2?family=VT323&display=swap";
body {
  background-color: #0c0c0c;
  width: 100vw;
  height: 200vh;
  margin: 0;
  overflow: visible;
}

nav {
  z-index: 1;
  background-color: #0c0c0c;
  width: 100vw;
  height: 5rem;
  position: sticky;
  top: 0;
}

ul {
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  grid-template-columns: repeat(4, 1fr);
  align-items: baseline;
  width: 100vw;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  transform: translateY(25%);
}

li {
  position: relative;
}

a {
  color: #f9f2ea;
  text-decoration: none;
}

.menu-title {
  margin-bottom: 50px;
  font-family: VT323, monospace;
  font-size: 2.25rem;
}

.menu-options {
  font-family: Montagu Slab, serif;
  font-size: 1rem;
}

.menu-options:hover {
  color: #0c0c0c;
  background-color: #ff0;
  transition: all .3s;
}

#logo {
  width: 40px;
  height: 40px;
}

@media screen and (width >= 650px) {
  .nav1 {
    grid-area: 1 / 1 / 2 / 2;
    justify-content: space-between;
    padding-left: 5vw;
    display: flex;
  }

  .dropdown {
    visibility: hidden;
  }
}

@media only screen and (width <= 650px) {
  .nav1 {
    visibility: hidden;
    width: 0;
    height: 0;
  }

  .dropbtn {
    cursor: pointer;
    background-image: url("dropdown.4256ba34.png");
    border: none;
    width: 1.5rem;
    height: 1.5rem;
    margin-left: 5vw;
    transform: translateY(5px);
  }

  .dropdown {
    display: inline-block;
    position: relative;
  }

  .dropdown-content {
    z-index: 1;
    background-color: #0c0c0c;
    min-width: 160px;
    display: none;
    position: absolute;
    transform: translateY(10px);
  }

  .dropdown-content a {
    color: #f9f2ea;
    margin-left: 5vw;
    padding-top: 1vh;
    text-decoration: none;
    display: block;
  }

  .dropdown-content a:hover {
    background-color: #ff0;
  }

  .show {
    display: block;
  }
}

.nav2 {
  text-align: center;
  grid-area: 1 / 2 / 2 / 4;
}

.nav3 {
  text-align: right;
  grid-area: 1 / 4 / 2 / 5;
  padding-right: 5vw;
}

.items {
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  background-color: #0c0c0c;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(3, 1fr);
  width: 90vw;
  height: 600px;
  margin-top: 25px;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

#item1 {
  background-color: #dc143c;
  grid-area: 1 / 1 / 2 / 2;
}

#item2 {
  background-color: #dc143c;
  grid-area: 1 / 2 / 2 / 3;
}

#item3 {
  background-color: #dc143c;
  grid-area: 1 / 3 / 2 / 4;
}

#item4 {
  background-color: #dc143c;
  grid-area: 2 / 1 / 3 / 2;
}

#item5 {
  background-color: #dc143c;
  grid-area: 2 / 2 / 3 / 3;
}

#item6 {
  background-color: #dc143c;
  grid-area: 2 / 3 / 3 / 4;
}
